import { identifierModuleUrl } from "@angular/compiler";

export class Student {
  id: number;
  nom: string;
  prenom: string;
  telephone: string;
  mail: string;

  constructor (

    id?: number,
    nom?: string,
    prenom?: string,
    telephone?: string,
    mail?: string
   


   )  {
    this.id= id;
    this.nom= nom;
    this.prenom= prenom;
    this.telephone= telephone;
    this.mail= mail;
       
   }
}




