import {Injectable} from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import 'rxjs/add/operator/map'


import { Observable } from 'rxjs/Observable';
import { of } from 'rxjs/observable/of';
import { catchError, map, tap } from 'rxjs/operators';

import { Student } from '../model/student';
import { Teacher } from '../teacher';
import { Skills } from '../model/skills';
import { StudentSkills } from '../model/studentSkills';
import { Admin } from '../model/admin';



const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};

@Injectable()
export class StudentsService {


  // URL to web API
  private studentUrl = 'students/students.json';
  private teacherUrl = 'students/teachers.json';
  private skillsUrl = 'students/skills.json';
  private adminUrl = 'students/admin.json';
  private studentUrl1 = 'students/student.json';
  private studentSkillsUrl = 'students/studentsskills.json';
  private studentRestUrl ="http://localhost:8080/students/";
  private skillsRestUrl ="http://localhost:8080/skills/";
  private studentSkillRestUrl ="http://localhost:8080/studentsskills/";
  private studentSkillRestUrlone ="http://localhost:8080/studentsskills//studentsskills/{id}";
  
  private adminRestUrl="http://localhost:8080/admin/";

  public data = {};
  constructor(
    private http: HttpClient) {
  }
  addStudent (student: Student) {
    return this.http.post(this.studentRestUrl, student, httpOptions).subscribe(data => {
      alert('ok');
}, error => {
    console.log(JSON.stringify(error.json()));
})
  }

  addStudentSkill (studentSkill: StudentSkills) {
    return this.http.post<Student[]>(this.studentSkillRestUrl, studentSkill, httpOptions).subscribe(data => {
      console.log('ok');
}, error => {
    console.log(JSON.stringify(error.json()));
})
  }

  private extractData(res: Response) {
    let body = res.json();
          return body || {};
    }

  getStudent (studentId): Observable<Student[]> {
    return this.http.get<Student[]>(this.studentSkillRestUrl +"/"+ studentId )
      .pipe(
        catchError(this.handleError('getStudent', [])));
  }

  getStudentService (studentId): Observable<Student[]> {
    return this.http.get<Student[]>(this.studentSkillRestUrl)
      .pipe(
        catchError(this.handleError('getStudent', [])));
  }

  getAdmin(): Observable<Admin[]> {
    //console.log('BOO', this.http.get<Student[]>(this.studentUrl));
    return this.http.get<Admin[]>(this.adminRestUrl);
  }

  getUsers(): Observable<Student[]> {
    console.log('BOO', this.http.get<Student[]>(this.studentUrl));
    return this.http.get<Student[]>(this.studentUrl);
  }

  getSkills(): Observable<Skills[]> {
    //console.log('BOO', this.http.get<Skills[]>(this.studentUrl));
    return this.http.get<Skills[]>(this.skillsRestUrl);
  }
  getStudentSkills() : Observable<StudentSkills[]> {
    return this.http.get<StudentSkills[]>(this.studentSkillRestUrl)
  }


  getSt(): Observable<Student[]> {
    //console.log('BOO', this.http.get<Skills[]>(this.studentUrl));
    return this.http.get<Student[]>(this.studentRestUrl)
    .pipe(
      catchError(this.handleError('getStudents', [])));
  }

  
 

  getTeacher(): Observable<Teacher[]> {
    console.log('BOO', this.http.get<Teacher[]>(this.teacherUrl));
    return this.http.get<Teacher[]>(this.teacherUrl);
  }

  /** GET beers from the server */
  getStudents (): Observable<Student[]> {
    return this.http.get<Student[]>(this.studentUrl)
      .pipe(
        catchError(this.handleError('getStudents', [])));
  }

  getStudentsService (): Observable<Student[]> {
    return this.http.get<Student[]>(this.studentUrl)
      .pipe(
        catchError(this.handleError('getStudents', [])));
  }
  /**
   * Handle Http operation that failed.
   * Let the app continue.
   * @param operation - name of the operation that failed
   * @param result - optional value to return as the observable result
   */
  private handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {

      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead

      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }
}



