import { Component, OnInit } from '@angular/core';
import { Student } from '../model/student';
//import { StudentsSkillsService } from '../services/studentskills.service';
import { StudentsService } from '../services/students.service';

import {Router} from '@angular/router';
import { StudentSkills } from '../model/studentSkills';
import { SS } from '../model/SS';

@Component({
  selector: 'app-list-student',
  templateUrl: './list-student.component.html',
  styleUrls: ['./list-student.component.css']
})
export class ListStudentComponent implements OnInit {

  errorMessage: string;

  orderProp: string = name;

  imgUrl = "/assets/images/student.png"

  //students = [];
  students : Array<Student>=[];
  studentsR : Array<Student>=[];
  studensSkills :Array<StudentSkills>=[];
  ss : Array<SS>=[];

  constructor(private router: Router, private studentsService: StudentsService) { }

  ngOnInit() {
      this.getStdsSkills()
      //this.getStudents();
      this.getArrayValues(this.studentsR,this.students);

      //this.constructSS();
     
  }

  getStudents(): void {
    this.studentsService.getSt()
      .subscribe(students => {
        //this.getArrayValues(this.students,students);
       this.students = students;
        console.log("taille du tableau de students 1 " +this.students.length);
     
        if (students.length === 0) {
          this.errorMessage = 'Empty list of students';
        }else {
          this.errorMessage = '';
        }
      });

     
  }

  getStdsSkills():void {
    this.studentsService.getStudentSkills().subscribe(studensskills => {
      this.studensSkills=studensskills;
    })
  }
  private getArrayValues ( tabToFill : Array<any>, tabRef : Array<any>) {
    if (tabRef && tabRef.length && tabRef.length > 0) {
        for (  let i=0;i<tabRef.length;i++) {
            tabToFill.push(tabRef[i]);
        }
    }
}
 /*  getStudentSkillsBase() {
    
    this.studentsService.get_studentsskills()
    .subscribe((jsondata) => {
      this.construct(jsondata) ; }
    );

   
    console.log("taille du tableau de ss 1 " +this.ss.length); 
    
   } */

   construct(test:JSON) {
   /*  for(let i=0;i<this.studensSkills.length;i++) {
      let ss :SS;
      ss.student=this.studensSkills[i].Student;
      ss.skills=this.studensSkills[i].Skills;
      ss.note=this.studensSkills[i].note;
      this.ss.push(ss);
      
    } */
    console.log(test);
   }
   

  sendDetails(student) {
    this.router.navigate(['/studentDetail', student.id]);
  }

  getStudentSkills(student) {
    return Object.keys(student.skills).sort((a, b) => {
      if (student.skills[a] > student.skills[b]) {
        return -1;
      } else if (student.skills[b] > student.skills[a]) {
        return 1;
      } else {
        return 0;
      }
    }).slice(0, 5);
  }
}



