import { Student } from "./student";
import { Skills } from "./skills";

export class StudentSkills {
    id:number;
    Student : Student;
    Skills : Skills;
    note : number;

    constructor (

        id?: number,
        Student?: any,
        Skills?: any,
        note?: any
       
   
   
       )  {
           this.note=note;
           this.Skills=Skills;
           this.id=id;
           this.Student=Student;
           
       }
}


